body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', sans-serif;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),
              url('https://media.istockphoto.com/id/2009686025/photo/ai-bot-on-phone-machine-learning-digital-mind-technology-concept.webp?a=1&b=1&s=612x612&w=0&k=20&c=GB8H00Y7CcFRM2UDstZZf0vrHQIu-5ElIovRGJwo0IA=')
              no-repeat center center fixed;
  background-size: cover;
  height: 100vh;
  color: white;
  overflow-x: hidden;
}

/* === Layout Containers === */
.main-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
  transition: all 0.5s ease;
}

.half-screen {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 50%;
}

.full-screen-center {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100%;
}

/* === Main Container === */
.container {
  background-color: rgba(0, 0, 0, 0.85);
  padding: 20px;
  border-radius: 15px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
  max-width: 420px;
  width: 90%;
  text-align: center;
  position: relative;
  z-index: 2;
  transition: transform 0.3s ease;
}

.container:hover {
  transform: scale(1.02);
}

input, select, button, textarea {
  margin: 10px 0;
  padding: 10px;
  width: 90%;
  border-radius: 8px;
  border: none;
  font-size: 1rem;
}

button {
  background-color: #4e73df;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2e59d9;
}

/* === First Chibi AI (Top Right) === */
.chibi-ai-container {
  position: absolute;
  top: -60px;
  right: 10px;
  width: 90px;
  z-index: 5;
}

.chibi-ai {
  width: 100%;
  animation: float 3s ease-in-out infinite;
  transform-origin: center;
  transition: transform 0.3s ease-in-out;
  filter: drop-shadow(0 0 6px rgba(255, 255, 255, 0.3));
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-15px); }
  100% { transform: translateY(0px); }
}

/* === Bubble on Hover === */
.manga-bubble {
  display: none;
  position: absolute;
  top: -40px;
  right: 90px;
  background: white;
  color: black;
  padding: 8px 14px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: bold;
  font-family: 'Comic Sans MS', cursive;
  box-shadow: 2px 2px 8px rgba(0,0,0,0.2);
  transition: opacity 0.3s ease;
}

.chibi-ai-container:hover .manga-bubble {
  display: block;
  opacity: 1;
}

/* === Code Panel Styles === */
.code-panel {
  background: rgba(0, 0, 0, 0.85);
  color: #00ffcc;
  font-family: 'Courier New', monospace;
  padding: 20px;
  border-radius: 10px;
  margin: 30px;
  height: 80%;
  overflow-y: auto;
  white-space: pre-wrap;
  box-shadow: 0 0 15px rgba(0,0,0,0.5);
  position: relative;
}

/* === Second Chibi AI (Bottom Left) === */
.chibi-ai-right {
  position: absolute;
  bottom: 20px;
  left: 20px;
  width: 100px;
  animation: float 3s ease-in-out infinite;
}

.code-bubble {
  position: absolute;
  bottom: 110px;
  left: 130px;
  background: white;
  color: black;
  padding: 8px 14px;
  border-radius: 20px;
  font-size: 0.9rem;
  font-family: 'Comic Sans MS', cursive;
  box-shadow: 2px 2px 8px rgba(0,0,0,0.2);
}

/* === Back Button === */
.back-button {
  position: absolute;
  bottom: 20px;
  right: 30px;
  background: rgba(255, 255, 255, 0.1);
  color: white;
  padding: 6px 12px;
  border: 1px solid #ccc;
  border-radius: 8px;
  font-size: 0.8rem;
  cursor: pointer;
  transition: background 0.3s ease;
  width: auto;
  min-width: 80px;
}

.back-button:hover {
  background: rgba(255, 255, 255, 0.25);
}

/* === Loader Spinner === */
.loader {
  border: 5px solid #f3f3f3;
  border-top: 5px solid #4e73df;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  margin: 15px auto;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* === Typing animation === */
.typing {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.typing span {
  height: 10px;
  width: 10px;
  margin: 0 3px;
  background-color: #fff;
  border-radius: 50%;
  display: inline-block;
  animation: blink 1.4s infinite both;
}

.typing span:nth-child(2) {
  animation-delay: 0.2s;
}
.typing span:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes blink {
  0%, 80%, 100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}
